import React, { Component } from "react";
//import Jumbotron from "../components/Jumbotron";
import Card from "../components/Card";
//import Form from "../components/Form";
import Tibit from "../components/Tibit";
import Footer from "../components/Footer";
import API from "../utils/API";
import { Col, Row, Container } from "../components/Grid";
import { List } from "../components/List";
//import Alert from 'react-bootstrap/Alert'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'

class Add extends Component {
  state = {
    tibits: [],
    q: "",
    message: "Some message"
  };

  componentDidMount() {
    this.getAllTibits();
  }

  handleInputChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
    console.log(this.state.q);
  };

  getAllTibits = () => {
      API.getAllTibits()
        .then(res =>{
          console.log("getting all tibits")
          if (!(res.data.length === 0)){
            this.setState({
              tibits: res.data
            })
          }else {
            this.setState({
              tibits: [],
              message: "Database is empty"
            })
          }
        })
        .catch((err) =>
          this.setState({
            tibits: [],
            message: err.message
          })
        );
  };

  handleFormSubmit = event => {
    event.preventDefault();
    API.createTibit({
        action: "Great Action" + Date(),
        goal: "Some Awesome Goal",
        location: "Someplace in Tucson",
        date: Date.now(),
        note: "This is a longer string"
      }).then(() => this.getAllTibits());
  };

  handleTibitDelete = id => {
    const tibit = this.state.tibits.find(tibit => tibit._id === id);
    API.deleteTibit(tibit._id)
    .then(() => this.getAllTibits());
  };

  handleTibitUpdate = id => {
    const tibit = this.state.tibits.find(tibit => tibit._id === id);

    API.updateTibit(tibit._id, {
      action: "Read the best book ever! " + Date()
    }).then(() => this.findTibits());
  };

  handleTibitCreate = () => {
    API.createTibit({
      action: "Great Action" + Date(),
      goal: "Some Awesome Goal",
      location: "Someplace in Tucson",
      date: Date.now(),
      note: "This is a longer string"
    }).then(() => this.getAllTibits());
  };

  render() {
    return (
      <Container>
        <Row>
          {/* <List>
              {this.state.tibits.map(tibit => (
                <Alert variant="primary">
                  <Alert.Heading>{tibit.goal}</Alert.Heading>
                  <p>{tibit.action}</p>
                  <hr />
                  <p className="mb-0">{tibit.date}</p>
                </Alert>
                ))}
          </List>      */}
          <Col size="md-12">
            <Card title="Tibit Add" icon="far fa-add">
              {/* <Form
                handleInputChange={this.handleInputChange}
                handleFormSubmit={this.handleTibitCreate}
                q={this.state.q}
              /> */}

            <Form>
                <Row>
                    <Col size="md-4">
                    <Form.Group controlId="formBasicPassword">
                            <Form.Label>Goal</Form.Label>
                            <Form.Control type="text" placeholder="Enter target goal" />
                            <Form.Text className="text-muted">
                            What is the goal achieved by this action?
                            </Form.Text>
                        </Form.Group>
                    </Col>
                    <Col size="md-4">
                    <Form.Group controlId="formGridAddress1">
                        <Form.Label>Location</Form.Label>
                        <Form.Control placeholder="1234 Main St" />
                        <Form.Text className="text-muted">
                            Where did the action took place?
                        </Form.Text>                        
                    </Form.Group>
                    </Col>                    
                    <Col size="md-4">
                        <Form.Group controlId="formBasicPassword">
                            <Form.Label>Date</Form.Label>
                            <Form.Control type="date" placeholder="action date" />
                            <Form.Text className="text-muted">
                            When did you perform the action?
                            </Form.Text>
                        </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col size="md-12">
                    <Form.Group controlId="exampleForm.ControlTextarea1">
                        <Form.Label>Action</Form.Label>
                        <Form.Control type="text" placeholder="Capture your action here" />
                        <Form.Text className="text-muted">
                            What is the action?
                        </Form.Text>                    
                    </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col size="md-12">
                    <Form.Group controlId="exampleForm.ControlTextarea1">
                        <Form.Label>Note</Form.Label>
                        <Form.Control as="textarea" rows="2" />
                        <Form.Text className="text-muted">
                            Any detail about action?
                        </Form.Text>                        
                    </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col size="md-12">
                        <Button 
                            className="float-right" 
                            variant="primary" 
                            type="submit"
                            onClick={() => this.handleTibitCreate()}
                            >
                            Submit
                        </Button>
                    </Col>
                </Row>
            </Form>

            </Card>
          </Col>
        </Row>
        <Row>
          <Col size="md-12">
            <Card title="Added Tibits" icon="camera">
              {this.state.tibits.length ? (
                <List>
                  {this.state.tibits.map(tibit => (
                    <Tibit
                      key={tibit._id}
                      action={tibit.action}
                      goal={tibit.goal}
                      location={tibit.location}
                      date={tibit.date}
                      CreateButton={() => (
                        <button
                          onClick={() => this.handleTibitCreate()}
                          className="btn btn-primary btn-sm ml-2"
                        >
                          Create
                        </button>
                      )}
                      UpdateButton={() => (
                        <button
                          onClick={() => this.handleTibitUpdate(tibit._id)}
                          className="btn btn-success btn-sm ml-2"
                        >
                          Update
                        </button>
                      )}
                      DeleteButton={() => (
                        <button
                          onClick={() => this.handleTibitDelete(tibit._id)}
                          className="btn btn-primary btn-sm ml-2"
                        >
                          Delete
                        </button>
                      )}
                    />
                  ))}
                </List>
                ) : (
                <h2 className="text-center">{this.state.message}</h2>
              )}
            </Card>
          </Col>
        </Row>
        <Footer />
      </Container>
    );
  }
}

export default Add;
